import React from "react";
import PropTypes from "prop-types";
import { FaFolderOpen as IconLoad } from "react-icons/fa";
import ToolbarButton from "./toolbar-button";
import { browserUpload } from "../../utils/browser";
import { IDBroker } from "../../utils/export";

export default function ToolbarLoadSVGButton(
  { state },
  { translator, projectActions }
) {
  const RULER_LENGTH = 1000;
  const SCENE_WIDTH = 3000;
  const SCENE_HEIGHT = 2000;
  const WALL_HEIGHT = 259;
  const WALL_THICKNESS = 10;

  let loadProjectFromFile = (event) => {
    event.preventDefault();
    browserUpload().then((data) => {
      var parser = new DOMParser();
      var doc = parser.parseFromString(data, "image/svg+xml");

      var rulerObj = doc.querySelectorAll("#ruler line")[0];

      const rulerPixel =
        RULER_LENGTH /
        Math.sqrt(
          Math.pow(
            rulerObj.getAttribute("x1") - rulerObj.getAttribute("x2"),
            2
          ) +
            Math.pow(
              rulerObj.getAttribute("y1") - rulerObj.getAttribute("y2"),
              2
            )
        );

      var facadeDomObj = Array.prototype.slice.call(
        doc.querySelectorAll("#facade_x5F_sagamore line.st1")
      );

      var internalDomObj = Array.prototype.slice.call(
        doc.querySelectorAll("#internals_x5F_sagamore line.st11")
      );

      let facadeParsingData = getParsingData(rulerPixel, facadeDomObj);
      const internalParsingData = getParsingData(rulerPixel, internalDomObj);

      let facadeVertices = facadeParsingData.vertices;
      // Move facade to internal walls
      for (let i = 0; i < facadeParsingData.lines.length; i++) {
        const moveSize = getMoveSize(
          facadeParsingData.lines[i],
          facadeParsingData,
          internalParsingData
        );

        const inx1 = getIndexVertice(
          facadeParsingData,
          facadeParsingData.lines[i].vertices[0]
        );
        const inx2 = getIndexVertice(
          facadeParsingData,
          facadeParsingData.lines[i].vertices[1]
        );

        facadeVertices[inx1] = {
          id: facadeVertices[inx1].id,
          type: "",
          prototype: "vertices",
          name: "Vertex",
          misc: {},
          selected: false,
          properties: {},
          visible: true,
          x:
            moveSize.direction == 0
              ? moveSize.position
              : facadeVertices[inx1].x,
          y:
            moveSize.direction == 1
              ? moveSize.position
              : facadeVertices[inx1].y,
          lines: facadeVertices[inx1].lines,
          areas: [],
        };
        facadeVertices[inx2] = {
          id: facadeVertices[inx2].id,
          type: "",
          prototype: "vertices",
          name: "Vertex",
          misc: {},
          selected: false,
          properties: {},
          visible: true,
          x:
            moveSize.direction == 0
              ? moveSize.position
              : facadeVertices[inx2].x,
          y:
            moveSize.direction == 1
              ? moveSize.position
              : facadeVertices[inx2].y,
          lines: facadeVertices[inx2].lines,
          areas: [],
        };
      }
      // // // //
      // console.log("internalParsingData", internalParsingData.vertices);
      let vertices = facadeVertices.concat(internalParsingData.vertices);
      // console.log("vertices", vertices);
      let lines = facadeParsingData.lines.concat(internalParsingData.lines);

      let verticesJson = {};
      for (let i = 0; i < vertices.length; i++) {
        verticesJson[vertices[i].id] = vertices[i];
      }
      let linesJson = {};
      for (let i = 0; i < lines.length; i++) {
        linesJson[lines[i].id] = lines[i];
      }

      const jsonData = {
        unit: "cm",
        layers: {
          "layer-1": {
            id: "layer-1",
            altitude: 0,
            order: 0,
            opacity: 1,
            name: "default",
            visible: true,
            vertices: verticesJson,
            lines: linesJson,
            holes: {},
            areas: {},
            items: {},
            selected: {
              vertices: [],
              lines: [],
              holes: [],
              areas: [],
              items: [],
            },
          },
        },
        grids: {
          h1: {
            id: "h1",
            type: "horizontal-streak",
            properties: {
              step: 20,
              colors: ["#808080", "#ddd", "#ddd", "#ddd", "#ddd"],
            },
          },
          v1: {
            id: "v1",
            type: "vertical-streak",
            properties: {
              step: 20,
              colors: ["#808080", "#ddd", "#ddd", "#ddd", "#ddd"],
            },
          },
        },
        selectedLayer: "layer-1",
        groups: {},
        width: SCENE_WIDTH,
        height: SCENE_HEIGHT,
        meta: {},
        guides: { horizontal: {}, vertical: {}, circular: {} },
      };

      console.log("jsonData", jsonData);

      projectActions.loadProject(jsonData);
    });
  };

  const checkDupliVertices = (vertices, x, y) => {
    for (let i = 0; i < vertices.length; i++) {
      if (x == vertices[i].x && y == vertices[i].y) {
        return i;
      }
    }
    return -1;
  };

  const checkDupliLines = (lineArr, verID1, verID2) => {
    for (let i = 0; i < lineArr.length; i++) {
      if (
        lineArr[i].vertices[0] + lineArr[i].vertices[1] == verID1 + verID2 ||
        lineArr[i].vertices[1] + lineArr[i].vertices[0] == verID1 + verID2
      ) {
        return 1;
      }
    }
    return -1;
  };

  const getParsingData = (rulerPixel, domObj) => {
    var vertices = [];
    var lines = {};
    var lineArr = [];
    // var room = "";

    for (var i = 0; i < domObj.length; i++) {
      let verticesID = IDBroker.acquireID();
      let verticesIndex = checkDupliVertices(
        vertices,
        Math.floor(domObj[i].getAttribute("x1") * rulerPixel),
        Math.floor(SCENE_HEIGHT - domObj[i].getAttribute("y1") * rulerPixel)
      );

      let vertice1, vertice2;
      if (verticesIndex != -1) {
        // duplicated
        vertice1 = vertices[verticesIndex];
      } else {
        // not duplicated
        vertice1 = {
          id: verticesID,
          type: "",
          prototype: "vertices",
          name: "Vertex",
          misc: {},
          selected: false,
          properties: {},
          visible: true,
          x: Math.floor(domObj[i].getAttribute("x1") * rulerPixel),
          y: Math.floor(
            SCENE_HEIGHT - domObj[i].getAttribute("y1") * rulerPixel
          ),
          lines: ["maIUMKnv7l", "nhQAdYl8q"],
          areas: [],
        };
        vertices.push(vertice1);
      }

      verticesID = IDBroker.acquireID();
      verticesIndex = checkDupliVertices(
        vertices,
        Math.floor(domObj[i].getAttribute("x2") * rulerPixel),
        Math.floor(SCENE_HEIGHT - domObj[i].getAttribute("y2") * rulerPixel)
      );
      if (verticesIndex != -1) {
        // duplicated
        vertice2 = vertices[verticesIndex];
      } else {
        vertice2 = {
          id: verticesID,
          type: "",
          prototype: "vertices",
          name: "Vertex",
          misc: {},
          selected: false,
          properties: {},
          visible: true,
          x: Math.floor(domObj[i].getAttribute("x2") * rulerPixel),
          y: Math.floor(
            SCENE_HEIGHT - domObj[i].getAttribute("y2") * rulerPixel
          ),
          lines: ["maIUMKnv7l", "nhQAdYl8q"],
          areas: [],
        };
        vertices.push(vertice2);
      }

      const checkLine = checkDupliLines(lineArr, vertice1.id, vertice2.id);

      if (checkLine == -1) {
        const lineID = IDBroker.acquireID();

        lineArr.push({
          id: lineID,
          type: "wall",
          prototype: "lines",
          name: "Wall",
          misc: {},
          selected: false,
          properties: {
            height: { length: WALL_HEIGHT },
            thickness: { length: WALL_THICKNESS },
            textureA: "bricks",
            textureB: "bricks",
          },
          visible: true,
          vertices: [vertice1.id, vertice2.id],
          holes: [],
        });
      }
    }

    console.log("vertices", vertices);

    return {
      vertices: vertices,
      lines: lineArr,
    };
  };

  const getMoveSize = (line, externalData, internalData) => {
    let externalVerticesJson = [];
    for (let i = 0; i < externalData.vertices.length; i++) {
      externalVerticesJson[externalData.vertices[i].id] =
        externalData.vertices[i];
    }
    const vertice1 = externalVerticesJson[line.vertices[0]];
    const vertice2 = externalVerticesJson[line.vertices[1]];

    let distance = 99999999;
    let result = { direction: -1, position: -1 };
    for (let i = 0; i < internalData.vertices.length; i++) {
      if (vertice1.x == vertice2.x) {
        // x move
        if (Math.abs(internalData.vertices[i].x - vertice1.x) < distance) {
          distance = Math.abs(internalData.vertices[i].x - vertice1.x);
          result = {
            direction: 0,
            position: internalData.vertices[i].x,
          };
        }
      } else if (vertice1.y == vertice2.y) {
        // y move
        if (Math.abs(internalData.vertices[i].y - vertice1.y) < distance) {
          distance = Math.abs(internalData.vertices[i].y - vertice1.y);
          result = {
            direction: 1,
            position: internalData.vertices[i].y,
          };
        }
      }
    }

    return result;
  };

  const getIndexVertice = (parseData, id) => {
    for (let i = 0; i < parseData.vertices.length; i++) {
      if (parseData.vertices[i].id == id) {
        return i;
      }
    }
    return -1;
  };

  return (
    <ToolbarButton
      active={false}
      tooltip="Load project from SVG"
      onClick={loadProjectFromFile}
    >
      <IconLoad />
    </ToolbarButton>
  );
}

ToolbarLoadSVGButton.propTypes = {
  state: PropTypes.object.isRequired,
};

ToolbarLoadSVGButton.contextTypes = {
  projectActions: PropTypes.object.isRequired,
  translator: PropTypes.object.isRequired,
};
